.logo{
  display: block;
  flex-shrink: 0;
  width: 207px;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  position: relative;
  &__project{
    display: flex;
    align-items: center;
    margin-left: 178px;
    &-img{
      border-radius: 2px;
      height: 20px;
      width: 58px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      margin-right: 15px;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-link{
      text-decoration: underline;
    }
  }
  &__logo{
    margin-right: 57px;
    transform: translateY(-10px);
  }
  &__burger{
    display: none;
  }
  &__mob{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__info{
    display: flex;
    align-items: center;
    &-item{
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 15px;
      color: $gray;
      &:not(:last-child){
        margin-right: 23px;
      }
      svg{
        margin-right: 10px;
      }
    }
  }
  &__socials{
    display: flex;
    align-items: center;
  }
  &__social{
    flex-shrink: 0;
    &:not(:last-child){
      margin-right: 15px;
    }
    &:hover{
      img{
        transform: translateY(-7px);
      }
    }
    img{
      transition: all .3s ease;
    }
  }
  &__mail,
  &__phone{
    white-space: nowrap;
    font-weight: 500;
  }
  &__mail{
    font-size: 15px;
    line-height: 20px;
  }
  &__phone{
    font-size: 18px;
    line-height: 24px;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    flex-shrink: 0;
    padding: 0 32px;
    height: 58px;
    &:not(:last-child){
      margin-right: 17px;
    }
  }
}

.header-top{
  padding: 9px 0;
  background-color: $darkBg;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 19px;
  &__inner{
    display: flex;
    align-items: center;
  }
}

.header-bottom{
  padding: 20px 0 105px;
  position: relative;
  &__inner{
    min-height: 58px;
    display: flex;
    align-items: center;
  }
}

.nav{
  border-top: 1px solid rgba(#000,.05);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 32px 0 36px;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    svg{
      margin-right: 11px;
    }
    &-badge{
      border-radius: 22px;
      padding: 0 5px;
      height: 18px;
      min-width: 18px;
      display: flex;
      align-items: center;
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      color: #fff;
      align-self: flex-start;
      margin-left: 5px;
      flex-shrink: 0;
      &--green{
        background-color: $primary;
      }
      &--red{
        background-color: $red;
      }
    }
  }
}

.menu{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  width: 100%;
  top: 100%;
  left: 0;
  z-index: 10;
  background-color: $whiteBg;
  &__back{
    display: none;
  }
  &__inner{
    padding: 40px 0;
    position: relative;
    min-height: 500px;
    &:before{
      content: '';
      position: absolute;
      left: 428px;
      top: 0;
      width: 1px;
      height: 100%;
      background-color: #E9E9E9;
    }
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__tab{
    width: 428px;
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 167%;
    letter-spacing: 0.01em;
    height: 60px;
    position: relative;
    transition: all .3s;
    span,
    svg{
      position: relative;
      z-index: 2;
    }
    span{
      text-decoration: underline;
      transition: transform .3s;
    }
    svg{
      margin-right: 15px;
    }
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 40px 0 0 40px;
      width: 100%;
      height: 100%;
      background-color: $primary;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &:hover{
      cursor: pointer;

    }
    &--active{
      pointer-events: none;
      &~.menu__content{
        transition: all 0s;
        opacity: 1;
        pointer-events: unset;
      }
    }
    &:hover,
    &--active{
      color: #fff;
      path{
        fill: #fff;
      }
      span,
      svg{
        transform: translateX(20px);
      }
      &:before{
        opacity: 1;
      }
    }
  }
  &__content{
    position: absolute;
    top: 0;
    left: 428px;
    padding: 46px 0 50px 57px;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__link{
    font-weight: 500;
    font-size: 17px;
    line-height: 235%;
    letter-spacing: 0.01em;
    text-decoration-line: underline;
    display: block;
  }
}

._open-service{
  .menu{
    opacity: 1;
    pointer-events: unset;
  }
  .header__service-btn{
    pointer-events: none;
  }
}


@media screen and (max-width: 1500px){
  .header{
    &__logo{
      margin-right: 20px;
      width: 180px;
    }
    &__info{
      &-item{
        &:not(:last-child){
          margin-right: 12px;
        }
      }
    }
    &__social,
    &__btn{
      &:not(:last-child){
        margin-right: 8px;
      }
    }
    &__btn{
      padding: 0 20px;
    }
    &__project{
      margin-left: 55px;
    }
  }
}


@media screen and (max-width: 1350px){
  .header{
    &__mob{
      flex-grow: unset;
      margin-left: auto;
    }
    &__info{
      display: none;
    }
    &__phone,
    &__mail{
      margin: 0 15px;
    }
  }
}


@media screen and (max-width: 1100px){
  .header-top{
    display: none;
  }

  .header-bottom{
    padding: 15px 0;
  }

  .header{
    border-bottom: 1px solid rgba(#000,.05);
    &__logo{
      transform: translateY(-5px);
    }
    &__mob{
      padding: 20px;
      position: fixed;
      top: 89px;
      left: 0;
      width: 100%;
      height: calc(100vh - 89px);
      background-color: #fff;
      display: flex;
      flex-direction: column;
      align-items: unset;
      justify-content: unset;
      overflow-y: auto;
      transition: all .5s;
      opacity: 0;
      pointer-events: none;
    }
    &__burger{
      margin-left: auto;
      width: 24px;
      height: 20px;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 15;
      span{
        display: block;
        width: 100%;
        height: 1.31px;
        background: $primary;
        left: 0;
        transition: all .3s;
        &:nth-child(1){
          position: absolute;
          top: 0;
        }
        &:nth-child(3){
          position: absolute;
          bottom: 0;
        }
      }
      &--active{

      }
    }
    &__social{
      &:not(:last-child){
        margin-right: 15px;
      }
    }
    &__mail,
    &__phone{
      font-size: 22px;
      line-height: 120%;
    }
    &__mail{
      margin: 25px 0 0;
    }
    &__phone{
      margin: 10px 0 0;
    }
    &__buttons{
      margin-top: 40px;
      display: block;
      max-width: 400px;
    }
    &__btn{
      width: 100%;
      &:not(:last-child){
        margin: 0 0 15px;
      }
    }
  }

  .nav{
    padding: 0;
    order: -1;
    position: static;
    border: none;
    .container{
      padding: 0;
    }
    &__inner{
      flex-direction: column;
      justify-content: unset;
    }
    &__link{
      margin-right: auto;
      margin-bottom: 20px;
      font-size: 20px;
    }
  }

  .menu{
    height: calc(100vh - 89px);
    top: 89px;
    overflow-y: auto;
    &__inner{
      &:before{
        display: none;
      }
    }
    &__tab{
      width: 100%;
      &--active{
        pointer-events: unset;
      }
    }
    &__content{
      position: static;
      opacity: 1;
      pointer-events: unset;
      transition: unset;
      padding: 10px 20px 0;
      display: none;
    }
    &__back{
      display: flex;
      align-items: center;
      font-size: 20px;
      margin-bottom: 30px;
      font-weight: 500;
      svg{
        margin-right: 10px;
        width: 8px;
        height: auto;
      }
    }
  }

  ._open-burger{
    .header{
      &__burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-8px,5px);
          }
          &:nth-child(2){
            transform: scale(0);
          }
          &:nth-child(3){
            transform: rotate(45deg) translate(-8px,-5px);
          }
        }
      }
      &__mob{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}
