@import "../vars.scss";
.breadcrumbs {
    margin: 15px 0 34px;
    &__item {
        font-weight: 500;
        font-size: 13px;
        line-height: 18px;
        display: inline;
        color: $gray;
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                color: $gray;
                margin: 0 2px 0 3px;
            }
        }
    }
}
