.main{
  padding: 8px 0 40px;
  color: #fff;
  .swiper{
    &-container{
      overflow: hidden;
      position: relative;
      background: radial-gradient(105.35% 136.18% at 71.03% 34%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
      border-radius: 20px;
    }
    &-slide{
      display: flex;
      padding: 60px 55px 77px 135px;
    }
    &-button{
      &-prev{
        left: 15px;
      }
      &-next{
        right: 15px;
      }
    }
    &-pagination{
      position: static;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
      &-bullet{
        width: 6px;
        height: 6px;
        background: #C2C2C2;
        opacity: 0.5;
        margin: 0 7px !important;
        &-active{
          width: 10px;
          height: 10px;
        }
      }
    }
  }
  &__content{
    max-width: 490px;
    flex-shrink: 0;
    margin-right: 50px;
  }
  &__date{
    font-weight: 800;
    font-size: 17px;
    line-height: 259%;
  }
  &__title{
    margin-top: 70px;
    max-width: 450px;
  }
  &__desc{
    font-weight: 500;
    font-size: 18px;
    line-height: 144%;
    margin-top: 45px;
  }
  &__btn{
    margin-top: 87px;
    width: 201.46px;
    height: 58px;
    font-size: 16px;
  }
  &__img{
    width: 100%;
    max-width: 760px;
    &-wrap{
      position: relative;
      flex-grow: 1;
    }
  }
  &__brand{
    position: absolute;
    right: 0;
    top: -5px;
    max-width: 100%;
  }
  &__sale{
    position: absolute;
    width: 153px;
    height: 153px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 52px;
    font-weight: 800;
    background-color: $blue;
    top: 30px;
    left: 50%;
    transform: translate(-50%,0) rotate(15deg);
  }
}

.advantages{
  overflow: hidden;
  padding: 60px 0;
  &__slider{
    margin-top: 85px;
  }
  .swiper{
    &-container{
      padding-bottom: 85px;
    }
    &-wrapper{
      align-self: stretch;
    }
    &-slide{
      width: 364px;
      max-width: calc(100% - 10px);
      height: auto;
    }
  }
}

.custom-block{
  padding: 60px 0;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 68px;
  }
  &__link{
    padding: 0 32px;
    height: 58px;
    font-size: 16px;
    svg{
      margin-right: 12px;
    }
  }
  .swiper{
    &-scrollbar{
      margin-top: 90px;
    }
  }
}

.services{
  @media screen and (min-width: 992px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4, calc(25% - 22.5px));
        gap: 30px;
      }
      &-slide{
        margin: 0 !important;
        &:nth-child(1){
          grid-column: 1/3;
        }
        &:nth-child(5){
          grid-column: 2/4;
        }
      }
      &-scrollbar{
        display: none;
      }
    }
  }
}

.doctors,
.feedbacks{
  .slider .swiper{
    &-slide{
      width: 760px;
      max-width: calc(100% - 10px);
      height: auto;
    }
  }
}

.feedbacks{
  background-color: $whiteBg;
  padding: 110px 0 120px;
  margin-top: 80px;
  .custom-block__top{
    margin: 0;
  }
  .slider {
    margin-top: 70px;
    .swiper{
      &-wrapper{
        align-items: unset;
      }
      &-slide{
        height: 100%;
      }
    }
  }
}

.sites{
  margin-top: 45px;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 177px;
      height: auto;
    }
  }
}

.consult{
  background: radial-gradient(107.97% 139.55% at 73.65% 35.83%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
  color: #fff;
  &__inner{
    display: flex;
    align-items: stretch;
  }
  &__content{
    padding: 130px 0;
    max-width: 760px;
    flex-shrink: 0;
  }
  &__img{
    display: flex;
    align-items: flex-end;
    max-width: 604px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 60px;
    line-height: 83%;
    letter-spacing: 0.01em;
  }
  &__desc{
    font-size: 20px;
    margin-top: 20px;
    line-height: 140%;
    letter-spacing: 0.01em;
    max-width: 680px;
    a{
      font-weight: 800;
      white-space: nowrap;
    }
  }
  &__form{
    margin-top: 65px;
  }
  &__check{
    max-width: 450px;
    margin-top: 85px;
  }
  &__btn{
    margin-top: 85px;
    width: 348px;
    height: 78px;
    font-size: 18px;
    max-width: 100%;
    svg{
      margin-right: 20px;
    }
  }
}

.address{
  padding: 60px 0;
  &--bottom{
    padding-bottom: 0;
  }
  &__tags{
    margin-top: 32px;
  }
  &__content{
    margin-top: 57px;
    height: 730px;
    position: relative;
  }
  &__card{
    position: absolute;
    padding: 63px 43px 73px;
    background: #FFFFFF;
    border-radius: 20px;
    top: 50%;
    left: calc((100% - 1550px) / 2);
    transform: translateY(-50%);
    z-index: 2;
    width: 530px;
    @media screen and (max-width: 1610px){
      left: 30px;
    }
  }
  &__title{
    font-weight: 800;
    font-size: 30px;
    line-height: 113%;
  }
  &__items{
    margin-top: 43px;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 189%;
    &-icon{
      flex-shrink: 0;
      width: 20px;
      margin-right: 14px;
      img{
        width: 100%;
      }
    }
    &>*{
      line-height: inherit;
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
    margin-top: 50px;
  }
  &__btn{
    padding: 0 32px;
    height: 58px;
    font-size: 16px;
    &:not(:last-child){
      margin-right: 16px;
    }
  }
}

.map{
  height: 100%;
}

.notes{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 499px;
      height: auto;
      max-width: calc(100% - 10px);
    }
  }
}

.social{
  padding: 180px 0;
  overflow: hidden;
  &--about{
    padding: 60px 0;
  }
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
    margin: 30px auto 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 144%;
    letter-spacing: 0.01em;
    max-width: 630px;
  }
  &__content{
    position: relative;
    height: 760px;
    overflow: hidden;
    margin-top: 64px;
  }
  &__grid{
    width: 2280px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }
  .social-card{
    width: 350px;
    margin: 30px 30px 0 0;
  }
  &__block{
    padding: 85px 0;
    border-radius: 1px;
    background-color: $primary;
    color: #fff;
    width: 350px;
    height: 350px;
    margin: 30px 30px 0 0;
  }
  &__subtitle{
    font-weight: 500;
    font-size: 30px;
    line-height: 173%;
    text-align: center;
    flex-shrink: 0;
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 28px;
  }
  &__item{
    background: #FFFFFF;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 5px;
    &:hover{
      opacity: .8;
    }
  }
}


@media screen and (max-width: 1500px){
  .main{
    .swiper{
      &-slide{
        padding: 55px;
      }
      &-button{
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
    &__content{
      max-width: 435px;
      margin-right: 20px;
    }
    &__title{
      max-width: 400px;
      margin-top: 35px;
    }
    &__desc{
      font-size: 16px;
      margin-top: 20px;
    }
    &__brand{
      top: 0;
    }
    &__btn{
      margin-top: 35px;
    }
    &__img{
      &-wrap{
        padding-top: 55px;
      }
    }
    &__sale{
      top: 70px;
    }
  }

  .services{
    @media screen and (min-width: 992px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(3, calc(33.33% - 20px));
        }
        &-slide{
          margin: 0 !important;
          &:nth-child(4){
            grid-column: 2/4;
          }
          &:nth-child(5){
            grid-column: 1/3;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 1300px){
  .consult{
    &__content{
      max-width: 580px;
    }
    &__title{
      font-size: 50px;
    }
    &__desc{
      font-size: 18px;
    }
    &__form,
    &__check,
    &__btn{
      margin-top: 30px;
    }
    &__img{
      max-width: 500px;
    }
  }
}


@media screen and (max-width: 1100px){
  .main{
    &__sale{
      width: 100px;
      height: 100px;
      font-size: 35px;
    }
  }

  .consult{
    &__content{
      padding: 60px 0;
    }
  }

  .social {
    padding: 60px 0;
    &--about{
      padding: 30px 0;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-bottom: 20px;
    .swiper{
      &-container{
        border-radius: 10px;
      }
      &-slide{
        display: block;
        padding: 20px;
      }
      &-button{
        &-prev,
        &-next{
          display: none;
        }
      }
    }
    &__content{
      max-width: 100%;
      margin: 0 0 20px;
    }
    &__title{
      max-width: 100%;
      margin-top: 20px;
    }
    &__desc{
      margin-top: 10px;
    }
    &__btn{
      margin-top: 20px;
    }
    &__img{
      max-width: 500px;
    }
    &__sale{
      left: 20%;
    }
  }

  .advantages{
    padding: 30px 0;
    &__slider{
      margin-top: 40px;
    }
    .swiper{
      &-container{
        padding-bottom: 40px;
      }
    }
  }

  .custom-block{
    padding: 30px 0;
    &__inner{
      padding-bottom: 80px;
      position: relative;
    }
    &__top{
      margin-bottom: 40px;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
    }
    .swiper{
      &-scrollbar{
        margin-top: 40px;
      }
    }
  }

  .services,
  .doctors{
    .swiper{
      &-wrapper{
        align-self: stretch;
      }
      &-slide{
        width: 364px;
        max-width: calc(100% - 10px);
        height: auto;
      }
    }
  }

  .feedbacks{
    .custom-block__title{
      font-size: 30px;
      br{
        display: none;
      }
    }
    .slider{
      margin-top: 35px;
    }
  }

  .sites{
    margin-top: 35px;
  }

  .consult{
    &__content{
      flex-shrink: unset;
      width: 100%;
    }
    &__img{
      display: none;
    }
    &__title{
      font-size: 35px;
    }
  }

  .address{
    padding: 30px 0;
    &--bottom{
      padding-bottom: 0;
    }
    &__tags{
      margin-top: 5px;
    }
    &__content{
      height: unset;
      margin-top: 30px;
    }
    &__card{
      position: static;
      background-color: $whiteBg;
      transform: unset;
      width: 100%;
      padding: 20px;
      border-radius: 0;
    }
    &__items{
      margin-top: 20px;
      gap: 10px;
    }
  }

  .map{
    height: 400px;
  }

  .social{
    padding: 30px 0;
    &__content{
      margin-top: 30px;
    }

  }
}


@media screen and (max-width: 576px){
  .main{
    &__sale{
      width: 75px;
      height: 75px;
      font-size: 25px;
    }
  }

  .address{
    &__buttons{
      flex-wrap: wrap;
      margin-top: 30px;
    }
    &__btn{
      width: 100%;
      padding: 0;
      &:not(:last-child){
        margin: 0 0 10px;
      }
    }
  }

  .social {
    &__title,
    &__desc{
      text-align: left;
    }
    &__title{
      font-size: 30px;
    }
    &__desc{
      font-size: 15px;
      margin-top: 15px;
    }
    &__content{
      height: 530px;
      margin-top: 15px;
    }
    &__grid {
      width: 1800px;
    }
    &__subtitle{
      font-size: 24px;
    }
    .social-card,
    &__block{
      margin: 15px 15px 0 0;
      width: 250px;
    }
    &__block{
      height: 250px;
      padding: 45px 0;
    }
    &__item{
      margin: 0 2px;
    }
  }
}
