@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
@import 'components/grid';
.swiper-scrollbar{
    width: 100%;
    height: 1px;
    background: #EEEEEE;
    &-drag{
        background-color: $black;
    }
}

.rating {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    &--read-only{
        pointer-events: none;
    }
    &__item {
        transition: all .3s;
        font-size: 25px;
        color: #CFCFCF;
        &:not(:first-child) {
            margin-right: 6px;
        }
        &:hover {
            cursor: pointer;
            color: #F0BE42;
            &~.rating__item {
                color: #F0BE42;
            }
        }
    }
}

.rating[data-total-rating="1"] .rating__item:nth-child(n+5),
.rating[data-total-rating="2"] .rating__item:nth-child(n+4),
.rating[data-total-rating="3"] .rating__item:nth-child(n+3),
.rating[data-total-rating="4"] .rating__item:nth-child(n+2),
.rating[data-total-rating="5"] .rating__item:nth-child(n+1) {
    color: #F0BE42;
}

.fg {
    position: relative;
    margin: 0 0 45px;
    input {
        border: none;
        border-bottom: 1px solid $black;
        padding: 0;
        height: 76px;
        width: 100%;
        font-size: 20px;
        background-color: transparent;
        color: $black;
        &::placeholder {
            color: $black;
        }
    }
    &__icon{
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        &~input{
            padding-left: 40px;
        }
    }
    &--white{
        input{
            border-color: #fff;
            color: #fff;
            &::placeholder{
                color: #fff;
            }
        }
    }
}

.mcheck {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 11L11 17L21 8' stroke='%235CA649' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }
    label {
        padding-left: 63px;
        position: relative;
        font-size: 16px;
        min-height: 24px;
        line-height: 150%;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 4px;
            border: 1px solid #D9D9D9;
            width: 30px;
            height: 30px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    &--white{
        label:before{
            background-color: #fff;
            border-color: #fff;
        }
        input:checked+label::before {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 11L11 17L21 8' stroke='%231F2123' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
    }
}

.tags{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.tag{
    display: flex;
    align-items: center;
    padding: 0 16px;
    height: 38px;
    border-radius: 50px;
    border: $border;
    margin: 12px 8px 0 0;
    font-weight: 500;
    font-size: 16px;
    &--active{
        font-weight: 800;
        border-color: $black;
    }
    &:hover{
        border-color: $black;
        color: $black;
    }
}

.live-title{
    display: flex;
    &:after{
        content: '';
        width: 12px;
        height: 12px;
        display: block;
        border-radius: 50%;
        background-color: $primary;
        margin-left: 10px;
    }
}

.video--played{
    video{
        pointer-events: unset;
    }
    .video__play{
        display: none;
    }
}


@media screen and (max-width: 992px){
    .fg{
        margin-bottom: 20px;
    }
}
