.about{
  padding-bottom: 30px;
  &__grid{
    margin-top: 16px;
    display: grid;
    grid-template-columns: 49% 49%;
    gap: 2%;
  }
  &__content{
    border-radius: 20px;
    padding: 32px;
    background-color: $whiteBg;
  }
  &__link{
    font-size: 15px;
    line-height: 24px;
    font-weight: 500;
  }
  &__img{
    border-radius: 20px;
    overflow: hidden;
    img{
      border-radius: 20px;
      width: 100%;
      display: block;
    }
  }
  &__map-link{
    width: 187px;
    height: 58px;
    margin-top: 48px;
    font-size: 16px;
  }
}


.youtube-video{
  position: relative;
  padding-bottom: 61.3%;
  border-radius: 20px;
  overflow: hidden;
  iframe{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}


.about2{
  padding: 20px 0 60px;
  @media screen and (min-width: 992px){
    .about__grid{
      margin-top: 54px;
    }
    .about__content{
      padding: 44px 42px;
    }
  }
}

.clinics{
  &__desc{
    font-size: 24px;
    line-height: 192%;
    letter-spacing: 0.01em;
    margin-bottom: 24px;
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__content{
      padding: 16px;
      border-radius: 10px;
    }
  }

  .youtube-video{
    padding-bottom: 55%;
    border-radius: 10px;
  }

  .about2{
    padding-bottom: 30px;
  }
}
