.display1{
  font-weight: 800;
  font-size: 70px;
  line-height: 86%;
}

.display2{
  font-weight: 500;
  font-size: 47px;
  line-height: 121%;
}

.display3{
  font-weight: 500;
  font-size: 22px;
  line-height: 136%;
}

.primary-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.bold-text {
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.body-text{
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.01em;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 1500px){
  .display1{
    font-size: 60px;
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 40px;
  }

  .display2{
    font-size: 35px;
  }

  .body-text{
    font-size: 14px;
  }
}
