.advantage-card{
  border-radius: 20px;
  background-color: $whiteBg;
  padding: 69px 25px 30px;
  &__icon{
    height: 80px;
    img{
      max-height: 100%;
      display: block;
    }
  }
  &__title{
    margin-top: 53px;
  }
  &__desc{
    font-size: 15px;
    line-height: 160%;
    margin-top: 5px;
  }
}

.service-card{
  min-height: 465px;
  background-size: cover;
  background-position: center;
  border-radius: 20px;
  display: flex;
  align-items: flex-end;
  padding: 42px 37px;
  position: relative;
  overflow: hidden;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    background: linear-gradient(0deg, rgba(81, 89, 107, 0.8) 0%, rgba(202, 198, 212, 0) 49.89%);
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  &__title{
    position: relative;
    z-index: 2;
    font-weight: 500;
    font-size: 24px;
    line-height: 112%;
  }
}

.doctor-card{
  display: grid;
  grid-template-columns: calc(100% - 270px) 270px;
  border-radius: 20px;
  background-color: $whiteBg;
  &__img{
    height: 100%;
    border-radius: 20px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__content{
    padding: 38px 45px 45px;
    display: flex;
    flex-direction: column;
  }
  &__name{
    font-weight: 700;
    font-size: 24px;
    line-height: 125%;
  }
  &__prof,
  &__experience,
  &__desc{
    font-weight: 500;
    font-size: 16px;
    line-height: 162%;
    letter-spacing: 0.01em;
  }
  &__prof,
  &__experience{
    color: $primary;
  }
  &__prof{
    margin-top: 16px;
  }
  &__experience{
    span{
      font-weight: 700;
    }
  }
  &__desc{
    margin-top: 24px;
  }
  &__buttons{
    margin-top: auto;
    padding-top: 20px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 32px;
    height: 58px;
    svg{
      margin-right: 12px;
    }
    &:not(:last-child){
      margin-right: 10px;
    }
  }
}

.doctor-card2{
  display: flex;
  flex-direction: column-reverse;
  .doctor-card{

    &__img{
      position: relative;
      overflow: hidden;
      padding-bottom: 96%;
      img{
        height: 133%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__content{
      padding: 25px 15px 45px;
      flex-grow: 1;
    }
    &__desc{
      margin-top: 16px;
    }
    &__buttons{
      padding-top: 30px;
    }
  }
}

.feedback-card{
  background-color: #fff;
  padding: 37px 45px;
  border-radius: 20px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__by{
    font-size: 22px;
  }
  &__for{
    font-size: 17px;
  }
  &__by,
  &__for{
    font-weight: 700;
    line-height: 120%;
  }
  &__top2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
  }
  &__left{
    display: flex;
    align-items: center;
  }
  &__site{
    margin-left: 30px;
    text-decoration-line: underline;
  }
  &__content{
    margin-top: 22px;
    transition: all .3s;
    &:not(._cut-text){
      .feedback-card__more{
        display: none;
      }
    }
  }
  &__text{
    transition: all .3s;
  }
  &__text,
  &__more{
    font-size: 17px;
    display: inline;
    line-height: inherit;
  }
  &__more{
    &._active{
      font-size: 0;
      &:before{
        content: 'скрыть';
        font-size: 17px;
      }
    }
  }
  &__answer{
    display: flex;
    align-items: center;
    margin-top: 22px;
    &-icon{
      margin-right: 17px;
      flex-shrink: 0;
      width: 29px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.feedback-card2{
  display: flex;
  flex-direction: column;
  &__img{
    height: 60px;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    padding-top: 30px;
    display: flex;
    align-items: center;
    margin-top: auto;
  }
  &__num{
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    margin-right: 20px;
  }
}

.note-card{
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 125%;
    letter-spacing: 0.01em;
    text-decoration-line: underline;
    margin-bottom: 32px;
    min-height: 60px;
    display: flex;
    align-items: flex-end;
  }
  &__img{
    position: relative;
    padding-bottom: 70%;
    background-size: cover;
    background-position: center;
    border-radius: 20px;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 47.67%, rgba(0, 0, 0, 0.5) 100%);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__badge{
    position: absolute;
    z-index: 2;
    top: 17px;
    left: 17px;
    padding: 0 14px;
    height: 32px;
    display: flex;
    align-items: center;
    color: #fff;
    border-radius: 23px;
    white-space: nowrap;
    font-weight: 500;
    font-size: 15px;
  }
  &__info{
    padding: 34px 26px;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-item{
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 15px;
      line-height: 16px;
      letter-spacing: 0.01em;
      color: #fff;
    }
    &-icon{
      margin-right: 10px;
      flex-shrink: 0;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__doctor{
    margin-top: 28px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name{
    font-weight: 700;
    font-size: 20px;
    letter-spacing: 0.01em;
  }
  &__avatar{
    flex-shrink: 0;
    margin-left: 4px;
    border-radius: 50%;
    width: 98px;
    height: 98px;
    border: 1px dashed $primary;
    padding: 7px;
    &-inner{
      border-radius: 50%;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__desc{
    margin-top: -9px;
    padding-right: 102px;
  }
  &__link{
    width: 147px;
    height: 58px;
    margin-top: 30px;
    font-size: 16px;
  }
}

.social-card{
  position: relative;
  &__video,
  &__img{
    padding-bottom: 100%;
    position: relative;
    video,
    img{
      pointer-events: none;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__video{
    &.video--played{
      &~.social-card__likes{
        display: none;
      }
    }
  }
  &__play{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($black,.7);
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    &-btn{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255, 255, 255, 0.4);
      backdrop-filter: blur(12px);
    }
  }
  &__likes{
    position: absolute;
    bottom: 22px;
    left: 22px;
    padding: 0 10px;
    display: flex;
    align-items: center;
    white-space: nowrap;
    background: #FFFFFF;
    border-radius: 23px;
    height: 32px;
    font-weight: 500;
    font-size: 15px;
    color: $primary;
    &:hover{
      cursor: pointer;
      opacity: .8;
    }
    &--active{
      font-weight: 800;
      .social-card__like{
        background-image: url(../img/heart2.svg);
      }
    }
  }
  &__like{
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    margin-right: 6px;
    background-image: url(../img/heart.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.clinic-card{
  display: grid;
  grid-template-columns: 42% 58%;
  border-radius: 20px;
  overflow: hidden;
  &__content{
    padding: 35px 26px 42px;
    border-radius: 20px;
    background-color: $whiteBg;
    position: relative;
    z-index: 2;
  }
  &__link{
    width: 147px;
    height: 58px;
    margin-top: 37px;
    font-size: 16px;
  }
  .map{
    height: 100%;
    width: calc(100% + 40px);
    margin-left: -40px;
    .map-widget-layout-view__ruler{
      opacity: 0;
    }
  }

  .address__items{
    gap: 20px;
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .service-card{
    min-height: 368px;
  }

  .doctor-card{
    display: flex;
    flex-direction: column-reverse;
    &__img{
      position: relative;
      overflow: hidden;
      padding-bottom: 96%;
      img{
        height: 133%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__content{
      padding: 25px 15px 45px;
      flex-grow: 1;
    }
    &__desc{
      margin-top: 16px;
    }
    &__buttons{
      padding-top: 30px;
    }
  }
}


@media screen and (max-width: 576px){
  .doctor-card{
    &__name{
      font-size: 20px;
    }
    &__buttons{
      flex-wrap: wrap;
    }
    &__btn{
      width: 100%;
      &:not(:last-child){
        margin: 0 0 10px;
      }
    }
  }

  .feedback-card{
    padding: 25px 15px 35px;
    &__top,
    &__top2{
      display: block;
    }
    &__for{
      margin-top: 10px;
    }
    &__date{
      margin-top: 10px;
    }
    &__text,
    &__more{
      font-size: 15px;
    }
    &__answer{
      align-items: unset;
      display: block;
      &-icon{
        margin: 0 0 10px;
        display: block;
      }
    }
  }

  .note-card{
    &__doctor{
      flex-direction: column-reverse;
      align-items: unset;
    }
    &__avatar{
      margin: 0 0 10px;
    }
    &__desc{
      margin-top: 10px;
      padding: 0;
    }
    &__info{
      padding: 15px;
    }
    &__img{
      border-radius: 10px;
    }
    &__badge{
      left: 15px;
    }
    &__title{
      font-size: 18px;
      min-height: 45px;
    }
  }

  .clinic-card{
    grid-template-columns: 100%;
    border-radius: 10px;
    &__content{
      border-radius: 0;
      padding: 20px;
    }
    &__map{
      order: -1;
      position: relative;
      padding-bottom: 100%;
      .map{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        margin: 0;
      }
    }
  }
}
