@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-primary,
.mbtn-black-outline,
.mbtn-white{
    border-radius: 50px;
    font-weight: 700;
    font-size: 15px;
    &:hover,
    &:active{
        color: #fff;
        path{
            fill:#fff;
        }
    }
    &:hover{
        border-color: $primaryHover;
        background-color: $primaryHover;
    }
    &:active{
        border-color: $primary;
        background-color: $primary;
    }
}

.mbtn-primary{
    background-color: $primary;
    color: #fff;
}

.mbtn-black-outline{
    border: 1px solid $black;
    color: $black;
}

.mbtn-white{
    background: #FFFFFF;
    color: $black;
}

.links-wrap{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 16px;
    .mlink{
        margin: 16px 60px 0 0;
    }
}

.mlink{
    font-size: 18px;
    line-height: 133%;
    text-decoration-line: underline;
    &.mlink--active{
        font-weight: 800;
    }
}


@media screen and (max-width: 992px){
    .links-wrap{
        .mlink{
            margin-right: 30px;
        }
    }
}
