.footer{
  padding: 80px 0 28px;
  background-color: $whiteBg;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    display: flex;
    flex-direction: column;
  }
  &__logo{
    width: 307px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 35px;
    }
  }
  &__title{
    font-weight: 800;
    font-size: 16px;
    line-height: 172%;
    color: $primary;
    margin-bottom: 12px;
  }
  &__link{
    font-weight: 500;
    font-size: 16px;
    line-height: 238%;
    display: block;
  }
  &__right{
    .footer__title{
      margin-bottom: 15px;
    }
    .footer__group{
      &:not(:last-child){
        margin-bottom: 42px;
      }
    }
  }
  .social__items{
    margin: 0 0 0 -5px;
    justify-content: unset;
  }
  .social__item{
    width: 25px;
    height: 25px;
    background-color: transparent;
  }
  &__phone{
    display: block;
    font-weight: 500;
    font-size: 22px;
    line-height: 164%;
  }
  &__clinic,
  &__mail,
  &__time{
    font-weight: 500;
    font-size: 18px;
    line-height: 144%;
  }
  &__time{
    font-size: 16px;
  }
  &__clinic{
    display: flex;
    align-items: center;
    svg{
      margin-right: 14px;
    }
    span{
      text-decoration: underline;
    }
  }
  &__logo2 {
    width: 117px;
    display: block;
    padding-top: 30px;
    margin-top: auto;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover svg>g {
      opacity: .6
    }
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 90px;
  }
  &__protect,
  &__privacy{
    font-weight: 500;
    font-size: 14px;
    line-height: 190%;
    color: #A0ABB7;
  }
  &__consult{
    font-family: $font2;
    font-size: 26px;
    line-height: 146%;
    color: #D1D5DA;
    letter-spacing: 7.5px;
  }
}


@media screen and (max-width: 1500px){
  .footer{
    &__consult{
      letter-spacing: 5px;
    }
  }
}


@media screen and (max-width: 1300px){
  .footer{
    &__logo{
      width: 210px;
    }
    &__link{
      font-size: 15px;
    }
    &__consult{
      letter-spacing: 2px;
      font-size: 23px;
    }
  }
}


@media screen and (max-width: 1100px){
  .footer{
    padding-top: 40px;
    &__links{
      display: none;
    }
    &__bottom{
      display: block;
    }
    &__consult{
      margin-top: 15px;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__top{
      display: block;
    }
    &__right{
      .footer__group{
        &:not(:last-child){
          margin-bottom: 25px;
        }
      }
      .footer__title{
        margin-bottom: 10px;
      }
    }
    &__logo2{
      margin: 0 0 30px;
    }
    &__bottom{
      margin-top: 45px;
    }
    &__consult{
      font-size: 20px;
    }
  }
}
